.container {
  width: 100%;
}

.controls {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: 1rem;
}

.positionControls {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 1rem;
}

.title {
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
}

.words {
  justify-content: center;
  font-size: 2rem;
  width: 100%;
  border: thick inset purple;
}

.prior,
.subsequent {
  color: #666;
  opacity: 0.6;
  width: 25%;
  padding: 1rem;
}

.prior {
  text-align: right;
  border-right: thin dashed purple;
}

.subsequent {
  text-align: left;
  border-left: thin dashed purple;
}

.current {
  font-weight: bold;
  text-align: center
}

.word {
  text-align: center;
}