* { 
  box-sizing: border-box; 
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: black;
  color: hsl(300, 84%, 45%);
  border-radius: 1rem;
}

body {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

#root {
  position: relative;
  height: 100%;
  width: 100%;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

h1, h2, h3 {
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}


button {
  text-align: center;
  border-radius: 1rem;
  border: thick double purple;
  background-color: black;
  font-size: 1.5rem;
  padding: 0.2rem;
}

button:hover {
  border-style: groove;
}

button:active {
  border-style: solid;
}


input {
  text-align: center;
  border-radius: 1rem;
  border: thick double purple;
  background-color: black;
  font-size: 1.5rem;
}

input::placeholder {
  color: rgb(0, 150, 150);
}

input:hover {
  border-style: groove;
}

input:focus {
  border-style: solid;
}


label {
  font-size: 1.5rem;
  text-align: right;
  padding-right: 1rem;
  border-radius: 1rem;
  border-right: thick double purple;
  border-bottom: thick groove purple;
  border-top: thin solid purple;
  margin: 0.1rem;
}


select {
  margin: 1rem;
  padding: 1rem;
  text-align: center;
  border-radius: 1rem;
  border: thick outset purple;
  background-color: rgb(121, 134, 132);
  font-size: 1.5rem;
}


textarea {
  min-height: 25%;
  background-color: black;
  color: purple;
  font-size: 1.5rem;
  border: thick double purple;
  border-radius: 1rem;
}

textarea:hover {
  border-style: groove;
}

textarea:focus {
  border-style: solid;
}